      <div class="md:mr-4 mx-0 w-full md:w-auto mb-3 md:mb-0">
        <h1 class="font-bold text-xxs uppercase text-left">Bitácora</h1>
        <select
          class="
            bg-gris-30
            trans
            rounded-md
            px-3
            h-8
            focus:outline-none
            w-full
            md:w-44
          "
          v-model="filtros.bitacora"
          @change="aplicarFiltro()"
        >
          <option value="TODOS">TODOS</option>
          <option
            v-for="(x, y) in arrayHistorico.bitacoras"
