          >
            {{ x.toUpperCase() }}
          </option>
        </select>
      </div>

      <div class="md:mr-4 mx-0 w-full md:w-auto mb-3 md:mb-0">
        <h1 class="font-bold text-xxs uppercase text-left">Parámetro</h1>
        <select
          class="
            bg-gris-30
            trans
