      </div>

      <div class="md:mr-4 mx-0 w-full md:w-auto mb-3 md:mb-0">
        <h1 class="font-bold text-xxs uppercase text-left">De la fecha:</h1>
        <input
          type="date"
          class="
            bg-gris-30
            trans
            rounded-md
            px-3
            h-8
            focus:outline-none
            w-full
            md:w-auto
          "
          v-model="filtros.fechaInicio"
          @change="obtenerArrayHistorico()"
        />
      </div>

      <div class="md:mr-4 mx-0 w-full md:w-auto mb-3 md:mb-0">
        <h1 class="font-bold text-xxs uppercase text-left">A la fecha:</h1>
        <input
          type="date"
          class="
            bg-gris-30
            trans
            rounded-md
            px-3
            h-8
            focus:outline-none
            w-full
            md:w-auto
          "
          v-model="filtros.fechaFin"
          @change="obtenerArrayHistorico()"
        />
      </div>

      <div
        class="
          relative
          flex
          items-center
          w-full
          md:w-auto
          px-10
          md:px-0
          mb-2
          md:mb-0
          mr-0
          md:mr-4
        "
      >
        <input
          type="seach"
          class="
            bg-gris-30
            trans
            w-full
            md:w-auto
            rounded-l-md
            pl-6
            h-8
            focus:outline-none
          "
          placeholder="Buscar..."
          v-model="filtros.palabra"
          @change="aplicarFiltro()"
        />
        <svg
          xmlns="http://www.w3.org/2000/svg"
          class="w-4 h-4 absolute top-0 mt-2 ml-1 text-gris-600"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          stroke-width="3"
          stroke-linecap="round"
          stroke-linejoin="round"
        >
          <circle cx="11" cy="11" r="8"></circle>
          <line x1="21" y1="21" x2="16.65" y2="16.65"></line>
        </svg>
        <button
          class="
            focus:outline-none
            bg-bluedemon-200
            rounded-r-md
            h-8
            w-8
            flex
            items-center
            justify-center
          "
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="w-4 h-4 text-bluedemon-400"
            viewBox="0 0 24 24"
            fill="none"
            stroke="currentColor"
            stroke-width="3"
            stroke-linecap="round"
            stroke-linejoin="round"
          >
            <circle cx="11" cy="11" r="8"></circle>
            <line x1="21" y1="21" x2="16.65" y2="16.65"></line>
          </svg>
        </button>
      </div>

      <div class="mx-0 w-10/12 md:w-auto">
        <button
          class="
            bg-indigo-200
            text-indigo-500
            h-8
            rounded-lg
            px-2
            text-xs
            w-10/12
            md:w-auto
            fw-500
            uppercase
          "
        >
          Gráficos
        </button>
      </div>
    </div>

    <!-- TABLA -->
    <div class="w-full overflow-hidden">
      <!-- component -->
      <div class="overflow-x-auto max-h-full">
        <div
          class="
            min-w-screen min-h-full
            flex
            items-start
            justify-center
            font-sans
          "
        >
          <div class="w-full lg:w-5/6">
            <div class="bg-white shadow-md rounded my-6">
              <table class="min-w-max w-full">
                <thead>
                  <tr
                    class="
                      bg-gray-200
                      text-gray-600
                      uppercase
                      text-sm
                      leading-normal
                    "
                  >
                    <th class="py-3 px-1 text-center">Bitácora</th>
                    <th class="py-3 px-1 text-center">Parámetro</th>
                    <th class="py-3 px-1 text-center hidden">Equipo</th>
                    <th class="py-3 px-1 text-center">UDM</th>
                    <th class="py-3 px-1 text-center">Hora solicitada</th>
                    <th class="py-3 px-1 text-center">Fecha de registro</th>
                    <th class="py-3 px-1 text-center">Hora de registro</th>
                    <th class="py-3 px-1 text-center">Registro</th>
                    <th class="py-3 px-1 text-center">fdr</th>
                    <th class="py-3 px-1 text-center">¿Incidencia?</th>
                  </tr>
                </thead>
                <tbody class="text-gray-600 text-sm">
                  <tr
                    v-for="(x, y) in arrayHistorico.data"
                    :key="y"
                    class="border-b border-gray-200 hover:bg-gray-100"
                  >
                    <!-- Bitácora -->
                    <td
                      class="
                        py-3
                        px-4
                        text-center
                        whitespace-nowrap
                        font-medium
                      "
                    >
                      {{ x.bitacora }}
                    </td>

                    <!-- Parámetro -->
                    <td
                      class="
                        py-3
                        px-4
                        text-center
                        whitespace-nowrap
                        font-medium
                        border-r border-l
                      "
                    >
                      {{ x.parametro }}
                    </td>

                    <!-- Equipo -->
                    <td
                      class="
                        py-3
                        px-4
                        text-center
                        whitespace-nowrap
                        font-medium
                        border-r border-l
                        hidden
                      "
                    >
                      {{ x.equipo }}
                    </td>

                    <!-- UDM -->
                    <td
                      class="
                        py-3
                        px-4
                        text-center
                        whitespace-nowrap
                        font-medium
                        border-r border-l
                      "
                    >
                      {{ x.medida }}
                    </td>

                    <!-- Hora -->
                    <td
                      class="
                        py-3
                        px-4
                        text-center
                        whitespace-nowrap
                        font-medium
                        border-r border-l
                      "
                    >
                      {{ x.horarioInput }}
                    </td>

                    <!-- Fecha -->
                    <td
                      class="
                        py-3
                        px-4
                        text-center
                        whitespace-nowrap
                        font-medium
                        border-r border-l
                      "
                    >
                      {{ x.fechaRegistro }}
                    </td>

                    <!-- Hora -->
                    <td
                      class="
                        py-3
                        px-4
                        text-center
                        whitespace-nowrap
                        font-medium
                        border-r border-l
                      "
                    >
                      {{ x.horaRegistro }}
                    </td>

                    <!-- Registro -->
                    <td
                      class="
                        py-3
                        px-4
                        text-center
                        whitespace-nowrap
                        font-medium
                        border-r border-l
                        relative
                      "
                    >
                      <input
                        type="number"
                        class="
                          absolute
                          top-0
                          left-0
                          h-full
                          w-full
                          py-1
                          focus:outline-none
                          font-bold
                          text-center text-red-500
                        "
                        v-model="x.valor"
                        @change="capturar(x)"
                      />
                    </td>

                    <!-- fdr -->
                    <td
                      class="
                        py-3
                        px-4
                        text-center
                        whitespace-nowrap
                        font-medium
                        border-r border-l
                        uppercase
                      "
                    >
                      {{ x.fueraRango }}
                    </td>

                    <!-- ¿Incidencia -->
                    <td
                      class="
                        py-3
                        px-4
                        text-center
                        whitespace-nowrap
                        font-medium
                        border-r border-l
                        uppercase
                      "
                    >
                      {{ x.crearIncidencia }}
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- TABLA -->
  </div>
</template>

<script>
import { computed, onMounted, ref } from "@vue/runtime-core";
import { useStore } from "vuex";
export default {
  props: {},

  setup() {
    const store = useStore();
    const Swal = require("sweetalert2");
    const Toast = Swal.mixin({
      toast: true,
      padding: ".3rem",
      background: "#2c3e50",
      iconColor: "#fff",
      position: "top",
      customClass: {
        popup: "colored-toast",
      },
      width: "19rem",
      showConfirmButton: false,
      timer: 2700,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    });

    const date = new Date();
    const mes = date.getMonth() + 1;
    const dia = date.getDate();
    const month = mes < 10 ? "0" + mes : mes;
    const day = dia < 10 ? "0" + dia : dia;
    const hoy = date.getFullYear() + "-" + month + "-" + day;

    const filtros = ref({
      destino: "TODOS",
      bitacora: "TODOS",
      parametro: "TODOS",
      tipoEquipo: "TODOS",
      fechaInicio: hoy,
      fechaFin: hoy,
      palabra: "",
    });

    onMounted(async () => {
      await obtenerArrayHistorico();
    });

    const arrayHistorico = computed(() => {
      return store.state.arrayHistorico;
    });

    const obtenerArrayHistorico = async () => {
      try {
        const response = await store.dispatch("getConexion", {
          idDestino: localStorage.getItem("idDestino"),
          idUsuario: localStorage.getItem("usuario"),
          action: "conexion",
        });

        if (response) {
          store.dispatch("getArrayHistorico", {
            idDestino: localStorage.getItem("idDestino"),
            idUsuario: localStorage.getItem("usuario"),
            fechaInicio: filtros.value.fechaInicio,
            fechaFin: filtros.value.fechaFin,
            action: "historico",
          });
        }
      } catch (error) {
        console.log(error);
      }
    };

    const aplicarFiltro = () => {
      const array = store.state.arrayHistoricoFull;

      const arrayNuevo = array.filter((x) => {
        if (
          (x.destino.toUpperCase() === filtros.value.destino.toUpperCase() ||
            filtros.value.destino.toUpperCase() === "TODOS") &&
          (x.bitacora.toUpperCase() === filtros.value.bitacora.toUpperCase() ||
            filtros.value.bitacora.toUpperCase() === "TODOS") &&
          (x.parametro.toUpperCase() ===
            filtros.value.parametro.toUpperCase() ||
            filtros.value.parametro.toUpperCase() === "TODOS") &&
          (x.tipoEquipo.toUpperCase() ===
            filtros.value.tipoEquipo.toUpperCase() ||
            filtros.value.tipoEquipo.toUpperCase() === "TODOS") &&
          (x.destino
            .toUpperCase()
            .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            x.bitacora
              .toUpperCase()
              .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            x.parametro
              .toUpperCase()
              .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            x.medida
              .toUpperCase()
              .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            x.horarioInput
              .toUpperCase()
              .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            x.fechaRegistro
              .toUpperCase()
              .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            x.horaRegistro
              .toUpperCase()
              .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            x.valor.indexOf(filtros.value.palabra) > -1 ||
            x.enTiempo
              .toUpperCase()
              .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            x.crearIncidencia
              .toUpperCase()
              .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            filtros.value.palabra === "")
        ) {
          return true;
        }
      });
      store.state.arrayHistorico.data = arrayNuevo;
    };

    const capturar = (equipo) => {
      try {
        const hoy = new Date();

        const data = {
          idDestino: localStorage.getItem("idDestino"),
          idUsuario: localStorage.getItem("usuario"),
          action: "capturar",
          idBitacora: equipo.idBitacora,
          idParametro: equipo.idParametro,
          idLineaTiempo: equipo.idLineaTiempo,
          idEquipo: equipo.idEquipo,
          idValor: equipo.idValor,
          equipo: equipo.equipo,
          medida: equipo.medida,
          parametroMinimo: equipo.parametroMinimo,
          parametroMaximo: equipo.parametroMaximo,
          valor: equipo.valor,
          crearIncidencia: equipo.crearIncidencia,
          idIncidencia: equipo.idIncidencia,
          fechaCaptura: equipo.fechaCaptura,
          tipoEquipo: equipo.tipoEquipo,
          horarioInput: equipo.horarioInput,
          fechaToken: equipo.fechaToken,
          status: equipo.status,
          enTiempo: equipo.enTiempo,
          fechaCapturaActual: `${hoy.getFullYear()}-${
            hoy.getMonth() + 1
          }-${hoy.getDate()} ${hoy.getHours()}:${hoy.getMinutes()}:${hoy.getSeconds()}`,
        };

        Swal.fire({
          title: "¿Desea Cambiar el Valor?",
          icon: "question",
          background: "#2c3e50",
          customClass: {
            popup: "colored-toast",
            backdrop: "swal2-backdrop-show",
            icon: "swal2-icon-show",
          },
          showDenyButton: true,
          showCancelButton: true,
          confirmButtonText: `Si, Cambiar`,
          denyButtonText: `No, Descartar`,
        }).then(async (result) => {
          /* Read more about isConfirmed, isDenied below */
          if (result.isConfirmed) {
            const response = await store.dispatch("getCaptura", data);

            if (response) {
              store.state.conexion = true;
              obtenerArrayHistorico();
              Toast.fire({
                icon: "success",
                title: "Captura Completada!",
              });
            }

            if (!response) {
              store.state.conexion = false;

              Toast.fire({
                icon: "info",
                title: "Intente de Nuevo!",
              });
            }
          } else if (result.isDenied) {
            Toast.fire({
              icon: "info",
              title: "Cambio Descartado!",
            });
          }
        });
      } catch (error) {
        store.state.conexion = false;
      }
    };

    return {
      arrayHistorico,
      filtros,
      obtenerArrayHistorico,
      aplicarFiltro,
      capturar,
    };
  },
};
</script>